import * as Type from './mutations-type'
const mutations={
 [Type.ADD_CART](state,book){
   let product=state.cartList.find(item=>item.bookId==book.bookId);
   if(product){
     product.bookCount+=1;
     state.cartList=[...state.cartList];
   }else {
     book.bookCount=1;
     state.cartList=[...state.cartList,book];
   }
 },
 [Type.REDUCE_CART](state,book){
   let product=state.cartList.find(item=>item.bookId==book.bookId);
   if(product){
    if(product.bookCount==0) return;
     product.bookCount-=1;
     state.cartList=[...state.cartList];
   }else {
     book.bookCount=1;
     state.cartList=[...state.cartList,book];
   }
 },
 [Type.REMOVE_CART](state,book){
   state.cartList=state.cartList.filter(item=>item.bookId!=book.bookId);
 },
 [Type.CLEAR_CART](state,book){
   state.cartList=[];
 },
 // [Type.STORE_CART](state,book){
 //   state.cartList=JSON.parse(localStorage.getItem('data'));
 // }
}
export default mutations



