import Vue from 'vue'
import Vuex from 'vuex'
import logger from 'Vuex/dist/logger'
Vue.use(Vuex);
import mutations from './mutations.js'
import getters from './getters.js'
let state={cartList:[ ]};

export default  new Vuex.Store({
  state,
  mutations,
  getters,
  strict:true,
  plugins:[logger()]
})





